.section-nav {
  $block: unquote("#{&}");
  padding: 1em 5%;
  @include for-size(tablet-landscape-up) {
    padding: 5em $spacer-inner;
  }
  &-list {
    padding: 0;
    margin: 0;
    &__item  {
      display: block;
      @include for-size(tablet-landscape-up) {
        display: inline-block;
        width: 20%;
      }
    }
  }
  &__container {
    background: $color-gray-light;
    display: block;
    margin: 1em;
    @each $sectionModifier, $styleElement in $sectionThemes {
      &--#{$sectionModifier} {
        background-image: url(map-get($styleElement, bgPatterns));
        background-color: map-get($styleElement, bgColor);
        background-position: 0 0;
        background-repeat: repeat;
        background-size: auto;
      }
      #{$block}__anchor {
        color: map-get($styleElement, textColor);
      }
    }
  }
  &__label {
    background-image: url('');
    border-bottom: 100px solid $color-cyan;
    border-left: 25px solid transparent;
    border-right: 25px solid transparent;
    height: 0;
    width: 100px;
  }
  &__anchor {
    color: #000;
    display: block;
    font-size: $font-x-large;
    text-transform: uppercase;
    overflow: auto;
    @include for-size(desktop-up) {
      //padding: 3em 1em 1em 1em;
      height: 160px;
    }
  }
  &__desc {
    color: #000;
    text-align: center;
    height: 40px;
    overflow: auto;
    margin: 0;
    padding: 0 1.5em;
  }
}
